import { Client, Databases, Query } from "appwrite";

class Service {
  endpoint = process.env.NEXT_PUBLIC_ENDPOINT;
  projectId = process.env.NEXT_PUBLIC_PROJECTID;
  databases: any;
  databaseId = process.env.NEXT_PUBLIC_DATABASEID;
  collectionId = process.env.NEXT_PUBLIC_COLLECTIONID;
  client = new Client();
  
  constructor() {
    if (!this.endpoint || !this.projectId) {
      throw new Error("Environment variables NEXT_PUBLIC_ENDPOINT or NEXT_PUBLIC_PROJECTID are not defined.");
    }
    this.client.setEndpoint(this.endpoint).setProject(this.projectId);
    this.databases = new Databases(this.client);
  }

  async GetPost(slug: string) {
    try {
        return await this.databases.getDocument(
            this.databaseId, // databaseId
            this.collectionId, // collectionId
            slug, // documentId
        );
    } catch (error) {
        console.log("Appwrite Error :: Get Post ::" ,error);
        return false;
    }
  }
  async ListPost(queries =[Query.equal("Status", ["Live"])]) {
    try {
        return await this.databases.listDocuments(
            this.databaseId, // databaseId
            this.collectionId, // collectionId
            queries, // queries
        );
    } catch (error) {
        console.log("Appwrite Error :: List Post ::" ,error);
        return false;
    }
  }

}


export const service = new Service();